interface productInput{
    name: string;
    brand: string;
}


const Validation = (input:productInput) =>{
    let errors: Partial<productInput> = {}
    
    if (!input.name){
        errors.name = "Name is necessary"
    };
    if (!input.brand){
        errors.brand = "Brand is necessary"
    }
    return errors;
}

export default Validation